@import url(https://fonts.googleapis.com/css2?family=Abril+Fatface&family=Open+Sans&display=swap);
:root {
  --light-text-color: white;
  --dark-text-color: black;
  --global-text-color: var(--dark-text-color);
  --default-fonts: 'Open Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Open Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  font-family: var(--default-fonts);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Abril Fatface', source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
  text-decoration: none;
  color: black;
}

.abril-fatface {
  font-family: 'Abril Fatface', 'Open Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  font-family: 'Abril Fatface', var(--default-fonts);
}

.App {
  min-height: 100vh;
}

.main-panel {
  align-items: center;
}

ul, li {
    margin: 0;
    padding: 0;
}

.navbar {
    /* background-color: #765D69; */
    width: 100hv;
    color: var(--global-text-color);
    padding: 0;
}

.nav-links li {
    display: inline-block;
    padding: 15px 40px 15px 40px;
}

.nav-links li a:hover {
    color: var(--global-text-color);
}

